/* eslint-disable react/prop-types */
import React from 'react';
import { graphql } from 'gatsby';

import { createUseStyles } from 'react-jss';
import Layout from '../components/Layout/Layout';
import SEO from '../components/Layout/SEO';

const useStyles = createUseStyles({
  wrapper: {
    position: 'relative',
    flexGrow: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  logo: {
    maxWidth: '80vw',
    maxHeight: '80vh',
    margin: 'auto',
  },
}, { name: 'indexPage' });

const normalizeSrcSets = ({
  data: { allFile: { nodes = [] } = {} } = {},
}) => nodes.reduce((obj, {
  childImageSharp: {
    fluid: { srcSet = null } = {},
  } = {},
  name,
  ...rest
} = {}) => ({
  ...obj,
  // [name]: srcSet,
  [name]: {
    ...rest,
    name,
    srcSet,
  },
}), {});

const IndexPage = (props) => {
  const classes = useStyles(props);
  const srcSets = normalizeSrcSets(props);
  return (
    <Layout>
      <SEO title="METAKRONISMS" />

      <div className={classes.wrapper}>
        <img
          className={classes.logo}
          srcSet={srcSets.MKS_LOGO_GR_1980.srcSet}
          alt="METAKRONISMS"
        />
        <div />
        <div />
      </div>
    </Layout>
  );
};
export const query = graphql`
  {
    allFile(filter: {extension: {in: ["png", "jpg", "jpeg"]}, dir: {regex: "/src\\/images$/"}}) {
      nodes {
        relativePath
        relativeDirectory
        root
        dir
        name
        extension
        childImageSharp {
          fluid {
            srcSet
          }
        }
      }
    }
  }
`;

export default IndexPage;
